import React, { useState } from 'react'
import Auction from '../section/Auction'
import CommentReception from '../section/CommentReception'
import VotingReception from '../section/VotingReception'

const Landing = () => {
  const [currentNav, setCurrentNav] = useState('comment-reception')

  const tabs = [
    { title: 'Comment Reception', slug: 'comment-reception' },
    { title: 'Voting Reception', slug: 'voting-reception' },
    { title: 'Auction', slug: 'auction' },
    { title: 'Ranking', slug: 'ranking' },
    { title: 'Archive', slug: 'archive' }
  ]

  return (
    <section className="landing">
      <div className="bg-light_background">
        <div className="n-container">
          <div className="flex justify-start">
            {tabs.map((row, key) => {
              return (
                <div
                  key={key}
                  className={`px-4 py-4 cursor-pointer border-b-4 hover:border-color2 ${
                    currentNav === row.slug
                      ? 'border-color2'
                      : 'border-transparent'
                  }`}
                  onClick={() => {
                    setCurrentNav(row.slug)
                  }}
                >
                  {row.title}
                </div>
              )
            })}
          </div>
        </div>
      </div>

      {currentNav === 'comment-reception' ? <CommentReception /> : ''}
      {currentNav === 'voting-reception' ? <VotingReception /> : ''}
      {currentNav === 'auction' ? <Auction /> : ''}
    </section>
  )
}

export default Landing
